	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad: function(options) {
		uni.request({
			url: 'http://localhost:8080/commodity/selectCom?ccategoryid=' + '',
			method: 'POST',
			success: res => {
				this.rightContent = res.data.message;
			}
		});
	},
	methods: {
		GouWu(){
			// 1 获取缓存中的购物车 数组
			let cart = uni.getStorageSync("cart") || [];
			// 2 判断 商品对象是否存在于购物车数组中
			let index = cart.findIndex(v => v.id === this.rightContent.id);
			if (index === -1) {
			  //3  不存在 第一次添加
			  this.rightContent.num = 1;
			  this.rightContent.checked = true;
			  cart.push(this.rightContent);
			} else {
			  // 4 已经存在购物车数据 执行 num++
			  cart[index].num++;
			}
			// 5 把购物车重新添加回缓存中
			uni.setStorageSync("cart", cart);
			// 6 弹窗提示
			uni.showToast({
			  title: '加入成功',
			  icon: 'success',
			  // true 防止用户 手抖 疯狂点击按钮 
			  mask: true
			});
		},
		XQ:function(e){

			uni.showToast({
				title: "编号" + e,
				icon: "none"
			});
			uni.navigateTo({
				url:'../ProductDetails/ProductDetails?id='+e
			})
		},
		change: function(e) {
			console.log(e);
			uni.showToast({
				title: "编号" + e.id,
				icon: "none"
			});
			uni.request({
				url: 'http://localhost:8080/commodity/selectCom?ccategoryid=' + e.id,
				method: 'POST',
				data: {},
				success: res => {
					this.rightContent = res.data.message;
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
};
